* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  a {
    color:#c4953d;
    text-decoration: none;
  }

  h3 {
      color: blanchedalmond;
      align-self: flex-start;
      text-align: end;
      padding-right: 10px;
      padding-top: 5px;
  }

  iframe {
    height: 100vh;
  }

  .section_content{
    display: flex;
    justify-content: space-around; 
    background: linear-gradient(  rgb(0, 10, 14),  rgb(2, 64, 88));
    flex-grow: 1;
    height: 100vh;
    width: 100%;
}


.fix_content_t {
  display: flex;
  flex-direction: column;
  margin-top: 80px;
  overflow-x: hidden;
  width: 80%;
  border-radius: 20px;
  box-shadow: inset 0 0px 10px 0  rgb(0, 10, 14),0 0px 10px 0  rgb(0, 10, 14);
  background: linear-gradient(  rgb(255, 255, 255),  rgb(2, 64, 88));
}

.fix_content_t::-webkit-scrollbar {
  display: none;
}


.title{
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: center;
  font-family: revert;
  color: rgb(1, 49, 68);
  margin: 30px;
}

.info_content {
  text-align: center;
  margin: 10px;
  font-family: sans-serif;
  font-size: 18px;
  color: rgb(40, 60, 68);
}

.image_container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.image{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.info {
  display: flex;
  justify-content: center;
  height: auto;
  width: auto;
}

.image img{
  margin: 30px;
  width: 80%;
  border-radius: 5%;
  box-shadow: 0 0px 10px 0  rgb(0, 10, 14),0 0px 10px 0  rgb(0, 10, 14);
}

