.header { 
    display: flex;
    flex-direction: column;
    background:  rgb(0, 10, 14);
  }
  
  .header__content {
    display: flex;
    justify-content: space-between;
    backdrop-filter: brightness(50%);
    border-bottom: 2px solid #684402;
  }

  .section {
    display: flex;

  }
  
  .heart {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 27px;
    color: #c4953d;
    width: 32%;
    height: 100%;
  }

  .links {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100px;
    height: 100%;
  }

  .links img {
    width: 100%;
  }
  
  .links:hover {
    box-shadow: inset 0 0px 20px 0 rgb(2, 64, 88),0 0px 20px 0 rgb(2, 64, 88);
  }