.section_content_platzi { 
    display: flex;
    justify-content: flex-end;
    width: 100%;
    height: 100vh;
    background:  rgba(0, 2, 78, 0.849);
}

.fix_content_p {
    display: flex;
    justify-content: flex-end;
    width: inherit;
}

.fix_content_p img {
    width: inherit;
    height: 100%;
}

.section_content_platzi a {
    display: flex;
    width: 30%;
    height: 10%;
    margin-top: 514px;
    justify-content: center;
    font-family: sans-serif;
    font-size: 30px;
    align-items: center;
    border: 1px solid #c4953d;
    position: absolute;
    background: linear-gradient( rgb(10, 1, 49),  rgba(0, 4, 128, 0.596));
}

.fix_content_p a:hover {
    border: 1px solid #ffcd70;
    color:#ffcd70;
}