.fix_content_section {
    display: flex;
    flex-direction: column;
    justify-content: center;
    overflow-x: hidden;
    width: inherit;
    height: auto;
}

.header__content_section {
    display: flex;
    justify-content: space-around;
    width: 100%;
    height: 50px;
    border-bottom: 2px solid #684402;
}

.links_section {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100px;
    height: 100%;
    
}

.links_section:hover {
    text-decoration: underline;
}

.section_container {
    display: flex;
    flex-direction: column;
    width: inherit;
    height: 100vh; 
    overflow-x: hidden;
}

.section_content_logo {
    display: flex;
    align-items: center;
    justify-content: center;
}

.section_content_logo img {
    width: 300px;
}

.section_container::-webkit-scrollbar {
    display: none;
}

.item_container {
    display: flex;
    align-items: flex-start;
    justify-content: space-around;
    margin-bottom: 30px;
    margin-top: 30px;
    width: inherit;
    overflow-y: hidden;
    height: 400px;
}

.item_info {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    text-align: center;
    width: 300px;
    height: 400px;
    border: 0.5px solid  #684402;
    background: linear-gradient(  rgb(0, 10, 14),  rgb(2, 64, 88));
    box-shadow: 0 0px 10px 0  rgb(0, 10, 14),0 0px 10px 0  rgb(0, 10, 14);
}

.item_info img {
    height: 200px;
}